<template>
  <div class="faceSuccess tc">
    <img class="img" src="../../assets/img/face/success.png"/>
    <div class="text1 font-bold">恭喜您已成为粉丝会员</div>
    <div class="text2">您可以通过加车方式成为俱乐部车主会员</div>
    <div class="btn1 font-bold" @click.stop='gotoAddCar'>我要加车</div>
    <div @click.stop="gotoRouter" class="btn2 font-bold box">跳过</div>
  </div>
</template>

<script>
  import bridge from 'fetch/bridge.js'
  export default{
    name:'faceSuccess',
    methods:{
      authAction(eventName){//埋点
        if(this.$route.query.test==1 || this.type=='app'){
          try{
            bridge('authAction',{eventName})
          }catch(err){
            console.log('埋点失败--',eventName)
          }
        }
      },
      gotoAddCar(){
        this.authAction('my_realname_submit_fans_add')
        this.$router.replace({name:'addCar',query:{type:this.$route.query.type,clientType:sessionStorage.isminiType}})
      },
      gotoRouter(){
        this.authAction('my_realname_submit_fans_pass')
        if(this.$route.query.type=='app'){//app跳转的
          bridge('finishPage')
        }else{
          if(sessionStorage.isminiType=='wechatMini'){
            //wx.miniProgram.redirectTo({ url: localStorage.getItem('myhref')});
            //wx.miniProgram.navigateTo({ url: localStorage.getItem('myhref')});
			wx.miniProgram.navigateBack()
          }else{
            window.location.replace(localStorage.getItem('myhref'))
          }
        }
      }
    }
  }
</script>

